<template>
    <form class="survey-form" @submit.prevent="submitForm">
        <div class="form-element">
            <h3>Please tell us a bit about yourself</h3>
            <input class="form-input" type="text" placeholder="Name (e.g., Ryan)" v-model.trim="name" />
            <input class="form-input" type="number" placeholder="Age (e.g., 31)" v-model="age" />
            <input class="form-input" type="text" placeholder="Profession (e.g., App Developer)" v-model.trim="profession" />
        </div>

        <div class="form-element">
            <h3>How satisfied are you with this product?</h3>
            <div>
                <input id="not-satisfied" name="satisfaction" type="radio" value="Not satisfied"  v-model="rating" />
                <label for="not-satisfied">Not satisfied</label>
            </div>
            <div>
                <input id="satisfied" name="satisfaction" type="radio" value="Satisfied"  v-model="rating" />
                <label for="satisfied">Satisfied</label>
            </div>
            <div>
                <input id="very-satisfied" name="satisfaction" type="radio" value="Very satisfied"  v-model="rating" />
                <label for="very-satisfied">Very satisfied</label>
            </div>
        </div>        
        <button>Submit</button>
    </form>
</template>

<script>
export default {
    data() {
        return {
            name: '',
            age: null,
            profession: '',
            rating: null,
        }
        
    },
    methods: {
        resetForm() {
            this.name = '';
            this.age = null;
            this.profession = '';
            this.rating = null;
        },
        submitForm() {
            if (
                this.name !== '' &&
                this.age !== null &&
                this.profession !== '' &&
                this.rating !== null
            ) {
                const formData = {
                    name: this.name,
                    age: this.age,
                    profession: this.profession,
                    rating: this.rating
                }
                
                fetch('/surveys/new', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify(formData)
                }).then(response => {
                    console.log(response)
                    if (response.ok) {
                        console.log(response.json())
                        this.resetForm();
                    } else {
                        throw new Error('Could not save data!')
                    }
                }).catch(error => {
                    console.log(error)
                })

                this.$router.push('/surveys')
            }
            
        }
    }
}
</script>

<style scoped>
.survey-form {
    height: 500px;
    width: 500px;
    background-color: #ddd;
    border-radius: 15px;
    margin-top: 100px;
    margin-left: calc(50% - 250px);
    padding: 20px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    float: left;
}

.form-element {
    margin-bottom: 20px;
}

h2, h3 {
    text-align: center;
    color: #292929;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.form-input {
    width: 80%;
    height: 40px;
    margin-bottom: 20px;
    border-radius: 5px;
    font-size: 20px;
    border: none;
    padding-left: 10px;
    margin-left: 10%;
}

input[type='radio'] {
  display: inline-block;
  width: auto;
  margin-left: 30%;
  margin-bottom: 10px;
  cursor: pointer;
}

button {
    color: #fff;
    height: 50px;
    width: 130px;
    background-color: #197BBD;
    border: none;
    cursor: pointer;
    font-size: 20px;
    border-radius: 5px;
    transition: .2s;
    margin-left: calc(50% - 65px);
}

button:hover {
    background-color: #F64740;
    transition: .2s;
}

</style>