<template>
    <ul>
        <survey-item
        v-for="survey in surveys"
        :key="survey.id"
        :name="survey.name"
        :age="survey.age"
        :profession="survey.profession"
        :rating="survey.rating"
        ></survey-item>
    </ul>
</template>

<script>
import SurveyItem from './SurveyItem.vue'

export default {
    components: {
        SurveyItem
    },
    data() {
        return {
            surveys: []
        }
    },
    methods: {
        loadSurveys() {
            fetch('/surveys/all')
                .then(response => {
                    if (response.ok) {
                        return response.json();
                    }
                }).then(data => {
                    const results = [];
                    for (const id in data) {
                        results.push({
                            id: id,
                            name: data[id].name,
                            age: data[id].age,
                            profession: data[id].profession,
                            rating: data[id].rating
                        })
                    }
                    this.surveys = results;
                })
                .catch(e => {
                    console.log(e)
                })
        }
    },
    created() {
        this.loadSurveys();
    }
}
</script>

<style scoped>
ul {
    list-style: none;
    float: left;
    width: 800px;
    height: calc(90vh - 60px);
    background-color: #ddd;
    border-radius: 15px;
    padding: 20px;
    margin-left: calc(50% - 400px);
    margin-top: 45px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}
</style>